header{
    height: 100vh;
    padding: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ====================== CV__Talk ======================= */

.cv_talk{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.4rem;
    justify-content: center;
}

/* ====================== Socials ======================= */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.4rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ====================== ME ======================= */

.me{
    background: linear-gradient(var(--color-primary), transparent) ;
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ====================== Scroll Down ======================= */


.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: .9rem;
}

/* ====================== for Medium devices ======================= */

@media screen and (max-width: 1024px) {
    header{
        height: 67vh;
    }
}

@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    
    .header__socials, .scroll__down{
        display: none;
    }

    .btn{
        font-size: 0.7rem;
    }
}